<template>
  <div class="orders">
    <p>我的订单</p>
    <div v-if="log">
      <div v-for="(item, index) in orders" :key="index">
      <van-card
        :num="list[index]"
        :price="10 * list[index] + '.00'"
        :desc="item.roderTime.substring(0, 19)"
        :title="item.shopName"
        thumb="https://img01.yzcdn.cn/vant/ipad.jpeg"
      >
        <template #footer>
          <van-button size="mini" type="default" @click="deleteOders(item.id)"
            >删除订单</van-button
          >
          <van-button size="mini" type="danger">结算</van-button>
        </template>
      </van-card>
    </div>
    </div>
    <div style=" margin-top: 30%;" v-else>
       <van-empty description="" image="search" />
       <span id="o-s">请登陆后查看</span>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import { Dialog } from "vant";
export default {
  data() {
    return {
      orders: [], //订单信息
      list: [], //每一个订单的商品数
      log:true,//是否登录状态,默认登录
    };
  },
  components: {
    [Dialog.Component.name]: Dialog.Component,
  },
  created() {
    if (sessionStorage.user != null) {
      this.getOrders();
    }else{
      this.log=false;
    }
  },
  methods: {
    getOrders() {
      //获取所有订单
      var id = sessionStorage.id;
      if (id) {
        var app = this;
        this.$http
          .get("/biz/queryOrdersByUserId?userId=" + id)
          .then(function (res) {
            // console.log(res.data);
            app.orders = res.data;
            for (var m = 0; m < res.data.length; m++) {
              app.list[m] = res.data[m].list.length;
            }
          });
      }
    },
    deleteOders(id) {
      Dialog.confirm({
        title: "你是否要删除该订单",
      })
        .then(() => {
          // on confirm
          var app = this;
          this.$http
            .get("/biz/deleteOrderByOid?orderId=" + id)
            .then(function (res) {
              console.log(res);
              if (res.data != null) {
                Toast.success("删除成功");
                app.getOrders();
              } else {
                Toast.fail("删除失败");
              }
            });
        })
        .catch(() => {
          // on cancel
        });
    },
  },
};
</script>

<style>
.orders {
  margin-top: -60px;
  text-align: left;
}
#o-s{
  text-align: center;
  display: block;
  font-size: 14px;
  margin-top: -20px;
  color: rgb(136, 130, 130);
}
.orders p {
  text-align: center;
  background-color: rgb(96, 173, 19);
  height: 50px;
  line-height: 50px;
  font-size: 20px;
}
.van-card__desc {
  margin-top: 20px;
}
</style>



