<template>
  <div class="shangjiaxinxi">
    <span id="fanhui"
      ><van-icon name="arrow-left" size="20" @click="fanhui"
    /></span>
    <div>
      <van-image
        width="8rem"
        height="8rem"
        fit="contain"
        :src="url + '/' + shop.photo"
      />
    </div>
    <van-row id="r-1">
      <van-col span="24">
        <b
          ><van-tag
            type="primary"
            size="medium"
            color="#ffff00"
            text-color="#000000"
            >品牌</van-tag
          ></b
        >
        <b> {{ shop.name }}</b>
      </van-col>
    </van-row>
    <van-row id="r-2">
      <van-col span="24">
        {{ shop.score }}
        月售{{ shop.sales }}单 硅谷专送 约{{ shop.deliveryTime }}分钟 距离{{
          shop.distance
        }}m
      </van-col>
    </van-row>
    <van-row id="r-3">
      <van-col span="24">
        <van-tag type="success">首单</van-tag>
        新用户下单立减17元(不与其他...)
        <van-tag type="danger" @click="tehui1">{{ H }}个优惠活动</van-tag>
        <van-popup
          v-model="showyouhui"
          position="bottom"
          closeable
          :style="{ height: '30%' }"
        >
          <div id="t-tehui">
            <h3>优惠活动</h3>
            <p v-for="(item, index) in tehui" :key="index">
              <van-tag color="#FF8C00"> {{ item.tag }} </van-tag>
              <b>[ {{ item.contents }} ]</b> 火烤菠萝皇堡
            </p>
          </div>
        </van-popup>
      </van-col>
    </van-row>
    <!-- tap切换 -->
    <van-tabs v-model="activeName" @click="onClick">
      <van-tab title="点餐" name="diancan">
        <div id="s-left">
          <van-sidebar v-model="activeKey" @change="onChangeT">
            <van-sidebar-item
              v-for="(item, index) in info"
              :key="index"
              :title="item.info"
              :to="'/shangjiaxinxi/' + item.info"
            />
          </van-sidebar>
        </div>
        <!-- 右侧商品 -->
        <div id="s-right">
          <div v-for="(item, index) in data" :key="index" v-show="show[index]">
            <van-row>
              <van-col span="7">
                <img
                  :src="url + '/' + item.photo"
                  alt=""
                  width="80px"
                  height="80px"
                />
              </van-col>
              <van-col span="17">
                <van-row class="r-1">
                  <van-col span="24">
                    <b> {{ item.name }}</b>
                  </van-col>
                </van-row>
                <van-row class="r-2">
                  <van-col span="7"> 月售80份 </van-col>
                  <van-col span="9"> 好评率100% </van-col>
                  <van-col span="8">
                    <van-stepper
                      :v-model="value[item.id]"
                      theme="round"
                      button-size="20"
                      disable-input
                      input-width="10px"
                      :default-value="0"
                      :min="0"
                      @plus="add(item.id)"
                      @minus="less(item.id)"
                    />
                  </van-col>
                </van-row>
                <van-row class="r-3">
                  <van-col>
                    <b>¥ 10 </b>
                  </van-col>
                </van-row>
              </van-col>
            </van-row>
          </div>
        </div>
        <van-submit-bar
          :price="1000 * list.length"
          button-text="提交订单"
          @submit="onSubmit"
        >
          <template #default>
            <div id="car">
              <van-icon
                name="cart-o"
                size="25"
                :badge="list.length"
                color="#ffffff"
              />
            </div>
          </template>
        </van-submit-bar>
      </van-tab>
      <van-tab title="评价" name="pingjia">
        <div>
          <div id="pingjia">
            <van-row>
              <van-col span="8">
                <van-row class="l-r1">
                  <van-col span="24"
                    ><b>{{ shop.goodsScore }}</b>
                  </van-col>
                </van-row>
                <van-row class="l-r2">
                  <van-col span="24"><b>综合评分</b></van-col>
                </van-row>
                <van-row class="l-r3">
                  <van-col span="24">高于周边商家99%</van-col>
                </van-row>
              </van-col>
              <van-col span="16">
                <van-row class="r-r1">
                  <van-col span="24">
                    <van-rate
                      :size="14"
                      v-model="shop.serviceScore"
                      readonly
                      allow-half
                    />
                    <b>服务评分</b><span> {{ shop.serviceScore }}</span>
                  </van-col>
                </van-row>
                <van-row class="r-r2">
                  <van-col span="24">
                    <van-rate
                      :size="14"
                      v-model="shop.score"
                      readonly
                      allow-half
                    />
                    <b> 商品评分</b><span> {{ shop.score }}</span>
                  </van-col>
                </van-row>
                <van-row class="r-r3">
                  <van-col span="24">
                    <b>送达时间</b> {{ shop.deliveryTime }}分钟
                  </van-col>
                </van-row>
              </van-col>
            </van-row>
          </div>
          <div>
            <van-tabs v-model="activepinglun" @click="onClickpinglun">
              <van-tab title="全部" name="all">
                <van-row
                  v-for="(item, index) in pinglun"
                  :key="index"
                  id="pinglunqu"
                >
                  <van-col span="3" offset="1">
                    <van-image
                      round
                      width="3rem"
                      height="3rem"
                      src="https://img01.yzcdn.cn/vant/cat.jpeg"
                    />
                  </van-col>
                  <van-col span="20">
                    <van-row class="c-1">
                      <van-col span="8">
                        {{
                          item.account.substring(0, 3) +
                          "****" +
                          item.account.substring(7, 11)
                        }}
                      </van-col>
                      <van-col span="16">
                        {{ item.oederTime.substring(0, 19) }}
                      </van-col>
                    </van-row>
                    <van-row class="c-2">
                      <van-col span="7" offset="1">
                        <van-rate
                          :size="14"
                          v-model="item.score"
                          readonly
                          allow-half
                        />
                      </van-col>
                      <van-col span="16"></van-col>
                    </van-row>
                    <van-row class="c-3">
                      <van-col span="24">
                        {{ item.comments }}
                      </van-col>
                    </van-row>
                  </van-col>
                </van-row>
              </van-tab>
              <van-tab title="满意" name="manyi">
                <div v-show="flag">
                  <van-row
                    v-for="(item, index) in pinglun"
                    :key="index"
                    id="pinglunqu"
                    v-show="isgood[index]"
                  >
                    <van-col span="3" offset="1">
                      <van-image
                        round
                        width="3rem"
                        height="3rem"
                        src="https://img01.yzcdn.cn/vant/cat.jpeg"
                      />
                    </van-col>
                    <van-col span="20">
                      <van-row class="c-1">
                        <van-col span="8">
                          {{
                            item.account.substring(0, 3) +
                            "****" +
                            item.account.substring(7, 11)
                          }}
                        </van-col>
                        <van-col span="16">
                          {{ item.oederTime.substring(0, 19) }}
                        </van-col>
                      </van-row>
                      <van-row class="c-2">
                        <van-col span="7" offset="1">
                          <van-rate
                            :size="14"
                            v-model="item.score"
                            readonly
                            allow-half
                          />
                        </van-col>
                        <van-col span="16"></van-col>
                      </van-row>
                      <van-row class="c-3">
                        <van-col span="24">
                          {{ item.comments }}
                        </van-col>
                      </van-row>
                    </van-col>
                  </van-row>
                </div>
              </van-tab>
              <van-tab title="不满意" name="bumanyi">
                <div v-show="flag">
                  <van-row
                    v-for="(item, index) in pinglun"
                    :key="index"
                    id="pinglunqu"
                    v-show="isgood[index]"
                  >
                    <van-col span="3" offset="1">
                      <van-image
                        round
                        width="3rem"
                        height="3rem"
                        src="https://img01.yzcdn.cn/vant/cat.jpeg"
                      />
                    </van-col>
                    <van-col span="20">
                      <van-row class="c-1">
                        <van-col span="8">
                          {{
                            item.account.substring(0, 3) +
                            "****" +
                            item.account.substring(7, 11)
                          }}
                        </van-col>
                        <van-col span="16">
                          {{ item.oederTime.substring(0, 19) }}
                        </van-col>
                      </van-row>
                      <van-row class="c-2">
                        <van-col span="7" offset="1">
                          <van-rate
                            :size="14"
                            v-model="item.score"
                            readonly
                            allow-half
                          />
                        </van-col>
                        <van-col span="16"></van-col>
                      </van-row>
                      <van-row class="c-3">
                        <van-col span="24">
                          {{ item.comments }}
                        </van-col>
                      </van-row>
                    </van-col>
                  </van-row>
                </div>
              </van-tab>
            </van-tabs>
          </div>
        </div>
      </van-tab>
      <van-tab title="商家" name="shangjia">
        <div class="t-3shangjiaxinxi">
          <h3>配送信息</h3>
          <p>
            <van-tag color="#7232dd">联想外卖</van-tag> 由商家配送提供配送,约{{
              shop.deliveryTime
            }}分钟送达,距离{{ shop.distance }}m
          </p>
          <p>配送费 ¥5</p>
        </div>
        <van-divider />
        <div class="t-3shangjiaxinxi">
          <h3>服务与活动</h3>
          <p v-for="(item, index) in tehui" :key="index">
            <van-tag color="#7232dd"> {{ item.tag }} </van-tag>
            {{ item.contents }}
          </p>
        </div>
        <van-divider />
        <div class="t-3shangjiaxinxi">
          <h3>商家实景</h3>
          <div>
            <van-image
              width="10rem"
              height="10rem"
              fit="contain"
              src="https://img01.yzcdn.cn/vant/cat.jpeg"
            />
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      value: [], //一个商品对应一个
      id: "", //传入的id
      data: [], //所有商品
      info: [], //分类菜单
      shop: [], //点击的商家
      showyouhui: false,
      url: "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT",
      activeName: "diancan", //tap切换
      activeKey: 0, //侧边导航
      i: 0, //侧边导航的当前下标
      show: [], //右边商品的显示隐藏
      pinglun: [], //评论信息
      activepinglun: "all", //评论tap
      isgood: [], //评论是否展示
      flag: false, //渲染决定条件,避免axios还未执行完毕,页面就渲染的问题
      list: [], //订单列表
      tehui: [], //商家特惠
      H: "", //特惠活动的个数
    };
  },
  created() {
    var id = this.$route.params.id;
    this.id = this.$route.params.id;
    this.getshangpinxinxi(id); //获取商品信息,以及菜单分类
    this.getshop(id); //获取顶部信息-点击的商家
    //默认
    this.onChangeM(id); //获取默认显示的菜单信息
    this.getpinglun(id); //获取评论信息
    this.gettehui(id); //获取商家的特惠活动,及数量
  },
  methods: {
    fanhui() {
      this.$router.push("/home");
    },
    getshop(id) {
      //顶部展示--点击的商家
      var app = this;
      this.$http
        .get("/biz/queryInfoByShopId?shopId=" + id)
        .then(function (res) {
          app.shop = res.data;
        });
    },
    getshangpinxinxi(id) {
      //获取所有商家,以及分类菜单
      var app = this;
      this.$http
        .get("/biz/queryAllFoodByShopId?shopId=" + id)
        .then(function (res) {
          app.data = res.data;
          for (var i = 0; i < res.data.length; i++) {
            app.value[i] = 0;
          }
          //获取左侧分类菜单,去重
          var person = res.data;
          function Es5duplicate(arr, type) {
            var newArr = [];
            var tArr = [];
            if (arr.length == 0) {
              return arr;
            } else {
              if (type) {
                for (var i = 0; i < arr.length; i++) {
                  if (!tArr[arr[i][type]]) {
                    newArr.push(arr[i]);
                    tArr[arr[i][type]] = true;
                  }
                }
                return newArr;
              } else {
                for (var i = 0; i < arr.length; i++) {
                  if (!tArr[arr[i]]) {
                    newArr.push(arr[i]);
                    tArr[arr[i]] = true;
                  }
                }
                return newArr;
              }
            }
          }
          app.info = Es5duplicate(person, "info");
        });
    },
    getpinglun(id) {
      //获取对应商家的评论
      var app = this;
      this.$http
        .get("/biz/queryCommentByShopId?shopId=" + id)
        .then(function (res) {
          app.pinglun = res.data;
        });
    },
    gettehui(id) {
      //获取商家的特惠活动
      var app = this;
      this.$http
        .get("/biz/querySpecialOfferByShopId?shopId=" + id)
        .then(function (res) {
          app.tehui = res.data;
          app.H = res.data.length;
        });
    },
    onClick(name, title) {
      //  tap切换
      if (name == "pingjia") {
        // console.log(this.pinglun);
      }
      //  console.log(title);
      //  console.log(name);
    },
    onSubmit() {
      //提交订单
      var list = this.list;
      var shopId = this.id; //商家id
      var userId = sessionStorage.id; //用户id
      // console.log(list);
      if (list.length > 0) {
        if (sessionStorage.id != null) {
          var obj = {
            userId: userId,
            shopId: shopId,
            list: list,
          };
          var app=this
          this.$http.post("/biz/insertOrder", obj).then(function (res) {
            if (res.data != null) {
              Toast.success("提交成功");
              app.$router.push("/orders");
              // console.log(res);
            } else {
              Toast.fail("提交失败");
            }
          });
        } else {
          Toast.fail("请登陆后购买");
        }
      } else {
        Toast.fail("请先添加商品");
      }
    },
    onClickpinglun(name, title) {
      //是否满意
      var id = this.id;
      var app = this;
      app.flag = false;
      this.$http
        .get("/biz/queryCommentByShopId?shopId=" + id)
        .then(function (res) {
          if (name == "manyi") {
            for (var m = 0; m < res.data.length; m++) {
              if (res.data[m].isGood == 1) {
                app.isgood[m] = true;
              } else {
                app.isgood[m] = false;
              }
            }
            app.flag = true;
          } else if (name == "bumanyi") {
            for (var m = 0; m < res.data.length; m++) {
              if (res.data[m].isGood == 0) {
                app.isgood[m] = true;
              } else {
                app.isgood[m] = false;
              }
            }
            app.flag = true;
          } else {
            for (var m = 0; m < res.data.length; m++) {
              app.isgood[m] = true;
            }
          }
        });
    },
    onChangeT(index) {
      //侧边导航切换
      this.i = index; //侧边导航的下标
      for (var m = 0; m < this.data.length; m++) {
        //循环遍历,使得点击左侧,右侧对应显示隐藏
        if (this.info[index].info == this.data[m].info) {
          this.show[m] = true;
        } else {
          this.show[m] = false;
        }
      }
      // Notify({ type: "primary", message: index });
    },
    onChangeM(id) {
      //设置起始侧边导航显示
      var app = this;
      this.$http
        .get("/biz/queryAllFoodByShopId?shopId=" + id)
        .then(function (res) {
          app.data = res.data;
          //起始侧边导航显示
          for (var m = 0; m < res.data.length; m++) {
            //循环遍历,使得点击左侧,右侧对应显示隐藏
            if (res.data[0].info == res.data[m].info) {
              app.show[m] = true;
            } else {
              app.show[m] = false;
            }
          }
        });
      // Notify({ type: "primary", message: index });
    },
    add(index) {
      //新增订单,这里只存数据
      // console.log(index); //食品id
      // console.log(this.id); //店铺id
      // console.log(sessionStorage.id); //用户id
      this.list.push({ foodId: index, buyCount: 1, shopId: this.id });
    },
    less(index) {
      //减少订单
      for (var i = 0; i < this.list.length; i++) {
        if (this.list[i].foodId == index) {
          this.list.splice(i, 1);
        } else {
        }
      }
    },
    tehui1() {
      this.showyouhui = true;
    },
  },
};
</script>

<style>
#fanhui {
  position: fixed;
  top: 40px;
  left: 20px;
}
.shangjiaxinxi {
  margin-top: -30px;
}
#t-tehui p {
  text-align: left;
  font-size: 16px;
  margin-left: 10%;
}
#r-1 {
  margin-top: -10px;
  font-size: 26px;
}
#r-2 {
  font-size: 14px;
  margin-top: 4px;
}
#r-3 {
  font-size: 14px;
  margin-top: 4px;
}
#s-left {
  width: 24%;
  float: left;
}
#s-right {
  overflow: hidden;
  width: 76%;
  text-align: left;
}
#s-right .r-1 {
  font-size: 18px;
}
#s-right .r-2 {
  margin-top: 10px;
  font-size: 12px;
}
#s-right .r-3 {
  margin-top: 6px;
  font-size: 14px;
  color: red;
}
#car{
 background-image: linear-gradient(to right, rgb(255, 96, 52), rgb(238, 10, 36));
  border-radius: 50px;
  width: 20%;
  padding: 7px;
  margin-left: -3%;
  margin-bottom: 2px;
}
/* 评价 */
#pingjia {
  margin-bottom: 15px;
}
#pingjia .l-r1 {
  font-size: 30px;
  color: orange;
}
#pingjia .l-r2 {
  font-size: 18px;
}
#pingjia .l-r3 {
  font-size: 14px;
  margin-left: 10px;
}
#pingjia .r-r1 {
  margin-top: 5px;
  font-size: 16px;
}
#pingjia .r-r1 span {
  margin-top: 5px;
  font-size: 15px;
  color: orange;
}
#pingjia .r-r2 {
  margin-top: 5px;
  font-size: 16px;
}
#pingjia .r-r2 span {
  margin-top: 5px;
  font-size: 15px;
  color: orange;
}
#pingjia .r-r3 {
  margin-top: 5px;
  font-size: 16px;
  text-align: left;
  margin-left: 10%;
}
#pinglunqu {
  margin-top: 10px;
}
#pinglunqu .c-1 {
  margin-top: 4px;
  font-size: 14px;
}
#pinglunqu .c-2 {
  margin-top: 6px;
  font-size: 14px;
}
#pinglunqu .c-3 {
  margin-left: 14px;
  margin-top: 6px;
  font-size: 12px;
  text-align: left;
}
/* t3商家信息 */
.t-3shangjiaxinxi {
  text-align: left;
  margin-left: 15px;
}
.t-3shangjiaxinxi h3 {
  margin-bottom: 5px;
}
.t-3shangjiaxinxi p {
  margin-left: 2px;
  font-size: 14px;
  margin: 5px 0px;
}
</style>



