<template>
  <div>
     <span id="fanhui">
      <van-icon name="arrow-left" size="20" @click="fanhui"
    /></span>
     <h2>硅谷外卖</h2>
    <div class="denglu">
      <van-form @submit="onSubmit">
        <van-field
          v-model="username"
          name="用户名"
          label="用户名"
          placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="密码"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <van-field
          v-model="yanzhengma"
          name="验证码"
          label="验证码"
          placeholder="验证码"
          :rules="[{ required: true, message: '请填写验证码' }]"
        />
        <div class="yanzheng">{{ yanzheng }}</div>
        <p><span id="denglu"></span></p>
        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit"
            >登录</van-button
          >
          <p>关于我们</p>
        </div>
      </van-form>
    </div>
  </div>
</template>
<script>
export default {
  name: "denglu",
  data() {
    return {
      username: "",
      password: "",
      yanzhengma: "",
      yanzheng: "",
      denglu:'',
    };
  },
  created() {
    this.getyanzhnegma();
  },
  methods: {
    onSubmit(values) {
      var params = {
        account: values.用户名,
        password: values.密码,
      };
      var ma = this.yanzheng;
      var app=this;
      this.$http.post("/user/login", params).then(function (res) {
        if (res.data) {
          if (values.验证码 == ma) {
            denglu.innerHTML = "";
            //传递参数,用于判断是否登录
            app.$router.push({name:"home", params:res.data});
          } else {
            denglu.innerHTML = "验证码不正确";
          }
        } else {
          denglu.innerHTML = "用户名或密码错误";
        }
      });
    },
    getyanzhnegma() {
      var app = this;
      this.$http.get("/user/sendCaptcha").then(function (res) {
        app.yanzheng = res.data;
      });
    },
     fanhui() {
      this.$router.go(-1);
    },
  },
};
</script>
<style>
#fanhui {
  position: fixed;
  top: 40px;
  left: 20px;
}
h2 {
  font-size: 50px;
  color: green;
  margin-top: 150px;
  margin-bottom: 80px;
}
.yanzheng {
  z-index: 1;
  position: relative;
  float: right;
  right: 50px;
  top: -30px;
}
#denglu {
  color: red;
}
.denglu {
  width: 300px;
  margin: 0 auto;
  margin-top: 30px;
}
</style>


