<template>
  <div>
    <span id="fanhui">
      <van-icon name="arrow-left" size="20" @click="fanhui"
    /></span>
    <h2>硅谷外卖</h2>
    <div class="zhuce">
      <van-form @submit="onSubmit">
        <van-field
          v-model="username"
          name="用户名"
          label="用户名"
          placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="密码"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <van-field
          v-model="password2"
          type="password"
          name="确认密码"
          label="确认密码"
          placeholder="确认密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit"
            >注册</van-button
          >
          <p>关于我们</p>
        </div>
      </van-form>
    </div>
  </div>
</template>
<script>
import { Toast } from "vant";
export default {
  name: "zhuce",
  data() {
    return {
      username: "",
      password: "",
      password2: "",
    };
  },
  methods: {
    onSubmit(values) {
      var params = {
        account: values.用户名,
        password: values.密码,
      };
      var app=this;
      if (values.密码 == values.确认密码) {
        this.$http.post("/user/register", params).then(function (res) {
          if (res.data == "register success") {
           Toast("注册成功");
            app.$router.push("/home");
          } else if (res.data == "register fail") {
           Toast("注册失败");
          } else if (res.data == "account is exit") {
           Toast("用户名已被注册");
          }
        });
      } else {
       Toast("两次密码不一致");
      }
    },
    fanhui() {
      this.$router.go(-1);
    },
  },
};
</script>
<style>
h2 {
  font-size: 50px;
  color: green;
  margin-top: 150px;
  margin-bottom: 80px;
}
#fanhui {
  position: fixed;
  top: 40px;
  left: 20px;
}
.zhuce {
  width: 300px;
  margin: 0 auto;
  margin-top: 50px;
}
</style>


