<template>
  <div>
    <van-nav-bar style="background-color: rgb(96, 173, 19);
height: 50px;
line-height: 50px;
font-size: 20px;" :fixed="true" title="首页" @click-right="onClickRight">
      <template #left>
        <van-icon name="search" size="18" @click="showPopup" />
      </template>
      <template v-if="use" #right>
        <p id="zhucedenglu"><router-link to="/user">个人中心</router-link></p>
      </template>
      <template v-else #right>
        <p id="zhucedenglu">
          <router-link to="/zhuce">注册</router-link>
          |
          <router-link to="/denglu">登录</router-link>
        </p>
      </template>
    </van-nav-bar>
    <van-popup v-model="show" position="top" :style="{ height: '8%' }">
      <form action="/">
        <van-search
          v-model="value"
          show-action
          placeholder="请输入搜索关键词"
          @search="onSearch"
          @cancel="onCancel"
        />
      </form>
    </van-popup>
    <!-- Swipe 轮播 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item class="swipe">
        <ul>
          <li v-for="(item, index) in swipe" :key="index">
            <img :src="url + '/' + item.photo" alt="" height="40px" /><br />
            <a href="">{{ item.name }}</a>
          </li>
        </ul>
      </van-swipe-item>
      <van-swipe-item class="swipe">
        <ul>
          <li v-for="(item, index) in swipe" :key="index">
            <img :src="url + '/' + item.photo" alt="" height="40px" /><br />
            <a href="">{{ item.name }}</a>
          </li>
        </ul>
      </van-swipe-item>
    </van-swipe>
    <!-- 所有商家 -->
    <div class="shop">
      <div
        v-for="(item, index) in shop"
        :key="index"
        @click="Shangjiaxinxi(item.id)"
      >
        <van-row>
          <van-col span="6" offset="1">
            <img
              :src="url + '/' + item.photo"
              alt=""
              width="100%"
              height="80px"
            />
          </van-col>
          <van-col span="17">
            <van-row>
              <van-col span="5" offset="1">
                <b
                  ><van-tag
                    type="primary"
                    size="medium"
                    color="#ffff00"
                    text-color="#000000"
                    >品牌</van-tag
                  ></b
                >
              </van-col>
              <van-col span="11">
                <b> {{ item.name }}</b>
              </van-col>
              <van-col span="2">
                <van-tag type="primary" color="#ffff00" text-color="#000000"
                  >保</van-tag
                >
              </van-col>
              <van-col span="2">
                <van-tag type="primary" color="#ffff00" text-color="#000000"
                  >准</van-tag
                >
              </van-col>
              <van-col span="3">
                <van-tag type="primary" color="#ffff00" text-color="#000000"
                  >票</van-tag
                >
              </van-col>
            </van-row>
            <div id="t1"></div>
            <van-row>
              <van-col span="17" offset="1" class="pingfen">
                <van-rate :size="10" v-model="item.score" readonly allow-half />
                <span> {{ item.score }}</span>
                <span id="xiaoliang">月销量{{ item.sales }}单</span>
              </van-col>
              <van-col span="6" class="pingfen">
                <van-tag color="#FFA500" plain>联想教育</van-tag>
              </van-col>
            </van-row>
            <div id="t2"></div>
            <van-row>
              <van-col span="21" offset="1">
                <span id="qisong">¥20元起送/配送费约¥5</span>
              </van-col>
              <van-col span="2">
                <span id="qisong">x2</span>
              </van-col>
            </van-row>
          </van-col>
        </van-row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      use: false, //是否登录,默认未登录
      show: false, //头部弹出层
      value: "", //搜索框的值
      url: "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT",
      swipe: [], //轮播
      shop: [], //商家
    };
  },
  created() {
    //
    if (sessionStorage.user != null) {
      this.use = true;
    }
    //页面加载
    this.getswipe();
    this.getshop();
  },
  methods: {
    getswipe() {
      //获取swipe
      var app = this;
      this.$http.post("/biz/queryBigCategory").then(function (res) {
        app.swipe = res.data;
      });
    },
    getshop() {
      //获取所有商家
      var app = this;
      this.$http.get("/biz/queryAllShopsInfo").then(function (res) {
        app.shop = res.data;
      });
    },
    Shangjiaxinxi(id) {
      //跳转页面获取商家信息
      this.$router.push({ name: "shangjiaxinxi", params:{id:id}});
    },
    onClickRight() {
      //点击右侧跳转到登录注册
    },
    showPopup() {
      //点击弹出弹出层
      this.show = true;
    },
    //搜索
    onSearch(val) {
      var app = this;
      this.$http
        .get("/biz/queryAllShopsInfoByName?name=" + val)
        .then(function (res) {
          app.shop = res.data;
        });
    },
    onCancel() {},
  },
};
</script>

<style>
.swipe li {
  display: inline-block;
  width: 25%;
  margin: 10px 0px;
}
.swipe a {
  display: inline-block;
  margin: 10px 0px;
  color: black;
}
/* 底层样式修改 */
.van-nav-bar__text {
  /* 右侧登录注册 */
  color: green;
}
.van-nav-bar .van-icon {
  /* 左侧搜索图标 */
  color: black;
}
#zhucedenglu a {
  color: green;
  margin: 2px;
}
.shop {
  text-align: left;
}
.pingfen span {
  font-size: 12px;
}
#xiaoliang {
  color: orange;
}
#t1 {
  height: 6px;
}
#t2 {
  height: 12px;
}
#qisong {
  font-size: 12px;
}
</style>



