<template>
  <div>
    <van-nav-bar
      style="
        background-color: rgb(96, 173, 19);
        height: 50px;
        line-height: 50px;
        font-size: 20px;
      "
      :fixed="true"
      title="我的"
    >
    </van-nav-bar>
    <van-row>
      <van-col span="6">
        <van-image
          round
          width="4rem"
          height="4rem"
          src="https://img01.yzcdn.cn/vant/cat.jpeg"
      /></van-col>
      <van-col span="16" class="user-name">
        <van-panel v-if="user" :title="user" desc="暂无绑定手机号">
          <span id="logout" @click="logout">退出登录</span></van-panel
        >
        <div v-else id="my-user">
          <p>
            <router-link to="/zhuce">注册</router-link>
            |
            <router-link to="/denglu">登录</router-link>
          </p>
          <van-panel title="" desc="暂无绑定手机号"> </van-panel>
        </div>
      </van-col>
    </van-row>
    <van-cell icon="label-o" is-link title="我的订单" @click="dingdan" />
    <van-cell icon="points" @click="daikaifa" is-link title="积分商城" />
    <van-cell icon="award-o" @click="daikaifa" is-link title="外卖会员卡" />
    <van-cell icon="hotel-o" @click="daikaifa" is-link title="服务中心" />
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      user: false,
      userid: "",
    };
  },
  created() {
    if (sessionStorage.user != null) {
      this.user = sessionStorage.user;
      this.usrid = sessionStorage.id;
    }
  },
  methods: {
    dingdan() {
      this.$router.push("/orders");
    },
    daikaifa() {
      Toast.fail("该内容还未开发");
    },
    logout() {
      //注销
      console.log(1);
      sessionStorage.removeItem('id');
      sessionStorage.removeItem('user');
        app.$router.push("/user");
    },
  },
};
</script>

<style>
#logout {
  font-size: 16px;
  position: fixed;
  top: 85px;
  right: 40px;
}
.van-cell__title {
  text-align: left;
  margin-left: 8px;
}
.user-name span {
  font-size: 20px;
}
#my-user {
  text-align: left;
}
#my-user p {
  margin-left: 20px;
  margin-bottom: 8px;
  margin-top: 8px;
}
</style>




