import Vue from "vue";
import Router from "vue-router";
import Home from "../components/Home";
import Search from "../components/Search";
import Orders from "../components/Orders";
import User from "../components/User";
import denglu from "../components/denglu";
import zhuce from "../components/zhuce";
import shangjiaxinxi from "../components/Shangjiaxinxi";
Vue.use(Router);

const routes = [
  { path: "/", component: Home },
  { path: "/home", name: "home", component: Home },
  { path: "/search", component: Search },
  { path: "/orders", component: Orders },
  { path: "/user", component: User },
  { path: "/denglu", component: denglu },
  { path: "/zhuce", component: zhuce },
  { path: "/shangjiaxinxi", name: "shangjiaxinxi", component: shangjiaxinxi },
  { path: "/shangjiaxinxi/:info", component: shangjiaxinxi, props: true }
];
// 创建路由管理器
const router = new Router({
  routes
  //路径模式
  // mode: 'hash'//默认hash(地址栏带#)
  // mode: "history" //(地址栏不带#)/
});

// 对外导出模块
export default router;



