<template>
  <div class="search">
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder="请输入搜索的商家信息"
        @search="onSearch"
        @cancel="onCancel"
      />
    </form>
     <div v-if="shop[0]" class="shop">
      <div v-for="(item, index) in shop" :key="index">
        <van-row>
          <van-col span="6" offset="1">
            <img
              :src="url + '/' + item.photo"
              alt=""
              width="100%"
              height="80px"
            />
          </van-col>
          <van-col span="17">
            <van-row>
              <van-col span="5" offset="1">
                <b
                  ><van-tag
                    type="primary"
                    size="medium"
                    color="#ffff00"
                    text-color="#000000"
                    >品牌</van-tag
                  ></b
                >
              </van-col>
              <van-col span="11">
                <b> {{ item.name }}</b>
              </van-col>
              <van-col span="2">
                <van-tag type="primary" color="#ffff00" text-color="#000000"
                  >保</van-tag
                >
              </van-col>
              <van-col span="2">
                <van-tag type="primary" color="#ffff00" text-color="#000000"
                  >准</van-tag
                >
              </van-col>
              <van-col span="3">
                <van-tag type="primary" color="#ffff00" text-color="#000000"
                  >票</van-tag
                >
              </van-col>
            </van-row>
            <div id="t1"></div>
            <van-row>
              <van-col span="17" offset="1"  class="pingfen">
                <van-rate :size="10" v-model="item.score" readonly allow-half />
                <span> {{ item.score }}</span>
                <span id="xiaoliang">月销量{{ item.sales }}单</span>
              </van-col>
               <van-col span="6" class="pingfen">
                <van-tag color="#FFA500" plain>联想教育</van-tag>
              </van-col>
            </van-row>
            <div id="t2"></div>
            <van-row>
              <van-col span="21" offset="1">
                <span id="qisong">¥20元起送/配送费约¥5</span>
              </van-col>
               <van-col span="2">
                <span id="qisong">x2</span>
              </van-col>
            </van-row>
          </van-col>
        </van-row>
      </div>
    </div>
     <div v-else>
      <van-empty description="" />
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
       value: "",
     shop: [],
      url: "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT",
    }
  },
   methods: {
    onSearch(val) {
      var app = this;
      this.$http
        .get("/biz/queryAllShopsInfoByName?name=" + val)
        .then(function (res) {
          app.shop = res.data;
        });
    },
    onCancel() {

    },
  },
}
</script>

<style>
.search{
  margin-top: -40px;
}
/* 底层样式 */
.van-empty{
  margin-top: 50%;
}
.shop {
  text-align: left;
}
.pingfen span{
  font-size: 12px;
}
#xiaoliang{
  color: orange;
}
#t1{
  height: 6px;
}
#t2{
  height: 12px;
}
#qisong{
  font-size: 12px;
}
</style>



