<template>
  <div id="app">
    <router-view />
    <div class="tianchong">
      <!-- 底层填充 -->
    </div>
    <div v-show="nav">
      <van-tabbar route>
        <van-tabbar-item replace to="/home" icon="home-o">外卖</van-tabbar-item>
        <van-tabbar-item replace to="/search" icon="search"
          >搜索</van-tabbar-item
        >
        <van-tabbar-item replace to="/orders" icon="orders-o"
          >订单</van-tabbar-item
        >
        <van-tabbar-item replace to="/user" icon="user-o">我的</van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      nav: true, //nav的显示隐藏,默认显示
    };
  },
  created() {
    if (this.$route.path === "/zhuce" || this.$route.path === "/denglu"){
      this.nav = false;
    } else {
      this.nav = true;
    }
  },
  watch: {
    // 监听 this.$route.path     watch监听非DOM元素的改变,注册登录界面隐藏nav
    "$route.path": function (newVal, oldVal) {
     if(this.$route.params.account!=null){
      sessionStorage.user=this.$route.params.account;
      sessionStorage.id=this.$route.params.id;
     }
      if (newVal === "/zhuce" || newVal === "/denglu") {
        this.nav = false;
      } else {
        this.nav = true;
      }
      //监听params:Object,来判断是否登录
    },
  },
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.tianchong {
  height: 50px;
}
</style>



